import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { MainService } from '../shared/services/main.service';
import { environment } from "../../environments/environment";
import Swal from 'sweetalert2';
// import * as io from 'socket.io-client';
// import { Socket } from 'socket.io-client';
import { Socket } from 'ngx-socket-io';


declare var $;

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit {

  sendOtpForm = document.getElementById('sendOtp');
  verifyOtpForm = document.getElementById('verify');
  form1:any = {}
  otp: any;
  comorbiditiesList:any = ["BP", "Diabetes", "Heart Disease", "Cancer", "Asthama", "Others"];
  symptomsList:any = [ "Fever", "Cold", "Throat Pain", "Loose Motions", "Headache", "Loss of Taste & Smell" ];
  genderList:any = [ "Male", "Female" ];
  itemList:any = [ "Oxygen Concentrators", "Pulse Oxymeters" ];
  form3:any;
  available: any = {};
  // socket: Socket;

  constructor(
    public route: ActivatedRoute,
    public router: Router,
    public mainService: MainService,
    private socket: Socket
  ) {
    // this.socket = io("http://164.52.210.197:4000");
  }

  ngOnInit() {
    this.otp = "";
    this.form3 = {
      name: "",
      gender: "",
      age: "",
      spo2: "",
      comorbidities: [],
      symptoms: [],
      covidSince: "",
      mobile: "",
      address: "",
      landmark: ""
    }

 this.mainService2()

    this.socket.emit("join", data => {
        console.log(data)
        // setResponse(data);
      });
    this.socket.on("availableStock", data => {
      // alert()
      console.log(data)
      setTimeout(()=>{
        this.mainService2()

      },1000)
      // setResponse(data);
    });
  }
mainService2(){
  this.mainService.getAvailableStock().subscribe(data => {
    if(data.output) {
      this.available.oc = data.output.available_oxygen_cylinder;
      this.available.po = data.output.available_oxy_meters;
    }
  });
}
  sendOTP(form) {
    this.mainService.sendOtp(form.value.mobile).subscribe(data => {
      // window.alert(data.output);
      let sendOtpForm = document.getElementById('sendOtp');
      let verifyOtpForm = document.getElementById('verify');
      $(sendOtpForm).hide();
      $(verifyOtpForm).show();
    })
  }

  onUpload(event) {
    console.log(event, this.form1.mobile);
    this.form3.image = event.target.files[0];
  }

  verifyOtp(otp) {
    console.log(otp.value)
    this.mainService.verifyOtp(this.form1.mobile, otp.value).subscribe(data => {
      if(data.output && data.output.status) {
        console.log(data);
        let verifyOtpForm = document.getElementById('verify');
        let detailsForm = document.getElementById('details');
        let cards = document.getElementById('cards');
        let forms = document.getElementById('forms');
        $(verifyOtpForm).hide();
        $(detailsForm).show();
      }
    });
  }

  onSubmit(form) {
    let item = form.value;
    item.image = this.form3.image;
    this.mainService.updateInfo(item, this.form1.mobile).subscribe(data => {
      if(data.output) {
        this.mainService.createBookings(this.form1.mobile, this.form1.need).subscribe(bookingData => {
          if(bookingData.output) {
            Swal.fire({
              title: 'Success!',
              text: 'Booked succesfully!',
              icon: 'success',
              confirmButtonText: 'Close'
            }).then(value => {
              location.reload();
            });
          } else {
            Swal.fire({
              title: 'Error!',
              text: 'Sorry! something went wrong',
              icon: 'error',
              confirmButtonText: 'Close'
            })
          }
        });
      } else {
        Swal.fire({
          title: 'Error!',
          text: 'Sorry! something went wrong',
          icon: 'error',
          confirmButtonText: 'Close'
        })
      }
    });
  }

}
