import { Component, OnInit } from '@angular/core';
import { MainService } from '../shared/services/main.service';
import { BtnCellComponent } from "./btn-cell/btn-cell.component";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import Swal from 'sweetalert2';
declare var $;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  volunteerForm: any = {};

  volColumnDefs = [
		{headerName: 'Name', field: 'name' },
		{headerName: 'Phone Number', field: 'phoneNumber' },
		{headerName: 'Alternate Phome Number', field: 'alt_phoneNumber'},
    {headerName: 'Action', cellRenderer: 'buttonRenderer', cellRendererParams: {parent: this, btn: ['viewV', 'edit']}, pinned: 'right', width: 120}
	];

  frameworkComponents: any;

  bookingColumnDefs = [
    {headerName: 'Patient\'s Name', field: 'user_details.Patient_Name', width: 150},
		{headerName: 'Phone Number', field: 'phoneNumber' , width: 150},
		{headerName: 'Oxygen Concentrator', field: 'Number_of_cylinder' , width: 150},
		{headerName: 'Pulse Oxydators', field: 'Number_of_monitorKid' , width: 150},
		{headerName: 'Status', field: 'status', width: 150},
		{headerName: 'Action', cellRenderer: 'buttonRenderer', cellRendererParams: {parent: this, btn: ['viewB', 'close']}, pinned: 'right', width: 120}
	];

  gridApi:any;
  dashboard: any = {};
  available: any = {};
  reports: any = {};
  volunteers: any[] = [];
  bookings: any[] = [];
  viewVar : any = {};
  stockForm:any = {};
  currentBooking: any;

  constructor(
    public route: ActivatedRoute,
    public router: Router,
    public mainService: MainService
  ) {
    this.frameworkComponents = {
      buttonRenderer: BtnCellComponent
    }
  }

  ngOnInit() {
    console.log(localStorage.getItem('login'))
    if(localStorage.getItem('login')) {
      this.getAllVolunteer();
      this.getAllBookings();
      this.getDashboard();
      this.getAvailableStock();
      this.getReports();
    } else {
      window.alert('Session expired! Please login');
      this.router.navigate(['login']);
    }
  }

  getAllVolunteer() {
    this.mainService.getAllVolunteer().subscribe(data => {
      if(data.output) {
        this.volunteers = data.output;
      }
    })
  }

  getAllBookings() {
    this.mainService.getAllBookings().subscribe(data => {
      if(data.output) {
        this.bookings = data.output;
      }
    })
  }

  getDashboard() {
    this.mainService.dashboard().subscribe(data => {
      if(data.output) {
        this.dashboard = data.output
      }
    })
  }

  getReports() {
    this.mainService.getReports().subscribe(data => {
      if(data.output) {
        this.reports = data.output;
      }
    })
  }

  getAvailableStock() {
    this.mainService.getAvailableStock().subscribe(data => {
      if(data.output) {
        this.available.oc = data.output.available_oxygen_cylinder;
        this.available.po = data.output.available_oxy_meters;
      }
    })
  }

  viewVolunteer(data) {
    console.log(data);
    this.viewVar = {
      type: 'V',
      title: 'Volunteer Details',
      content: [
        { label: "Name" , value: data.name },
        { label: "Phone Number", value: data.phoneNumber },
        { label: "Alternate Phone Number", value: data.alt_phoneNumber },
        { label: "Address Line 1", value: data.address1 },
        { label: "Address Line 2", value: data.address2 },
        { label: "District", value: data.district },
        { label: "State", value: data.state },
        { label: "Pincode", value: data.pincode }
      ]
    }
    $('#viewModal').modal('show');
  }

  viewBooking(data) {
    this.viewVar = {
      type: 'B',
      status: data.status,
      title: 'Booking Details',
      content: [
        { label: "Patient's Name", value: data.user_details.Patient_Name },
        { label: "Gender", value: data.user_details.Gender },
        { label: "Age", value: data.user_details.Age },
        { label: "SpO2(Oxygen) Level", value: data.user_details.SPO2 },
        { label: "Comorbidities", value: data.user_details.Comorbidities },
        { label: "Symptoms", value: data.user_details.Symptoms },
        { label: "Covid+ Since", value: data.user_details.CovidSince },
        { label: "Mobile No", value: data.user_details.phoneNumber },
        { label: "Address", value: data.user_details.Address },
        { label: "Landmark", value: data.user_details.Landmark },
        { label: "Booked At", value: new Date(data.create_time), pipe: 'date' }
      ]
    }
    if(data.approve_reject_date) {
      if(data.status == 'rejected') {
        this.viewVar.content.push({label: 'Rejected At', value: new Date(data.approve_reject_date), pipe: 'date'})
      } else {
        this.viewVar.content.push({label: 'Approved At', value: new Date(data.approve_reject_date), pipe: 'date'})
      }
    }
    if(data.exp_return_date && data.status != 'rejected') {
      this.viewVar.content.push({label: 'Expected Return Date', value: new Date(data.approve_reject_date), pipe: 'date', format: 'mediumDate'})
    }
    if(data.act_return_date && data.status != 'rejected') {
      this.viewVar.content.push({label: 'Actually Returned At', value: new Date(data.approve_reject_date), pipe: 'date'})
    }
    if(data.reason && data.status == 'rejected') {
      this.viewVar.content.push({label: 'Reason For Rejection', value: data.reason})
    }

    $('#viewModal').modal('show');
  }

  addVolunteer() {
    this.volunteerForm = {}
    $('#volunteerModal').modal('show');
  }

  editVolunteer(data) {
    this.volunteerForm = {
      name: data.name,
      phone: data.phoneNumber,
      altPhone: data.alt_phoneNumber,
      line1: data.address1,
      line2: data.address2,
      district: data.district,
      state: data.state,
      pincode: data.pincode
    }
    $('#volunteerModal').modal('show');
  }

  setCurrentBooking(item) {
    this.currentBooking = item;
  }

  getCurrentBooking() {
    return this.currentBooking;
  }

  approve() {
    this.mainService.approve(this.getCurrentBooking()).subscribe(data => {
      if(data.output) {
        Swal.fire({
          title: 'Success!',
          text: 'Approved a booking succesfully!',
          icon: 'success',
          confirmButtonText: 'Close'
        }).then(value => {
          this.getAllBookings();
          this.getAvailableStock();
        });
      }
    })
  }

  reject() {
    Swal.fire({
      title: 'Reason rejection',
      input: 'text',
      inputAttributes: {
        autocapitalize: 'off'
      },
      showCancelButton: true,
      confirmButtonText: 'Reject',
      confirmButtonColor: "#dc3545",
      showLoaderOnConfirm: true,
      allowOutsideClick: () => !Swal.isLoading()
    }).then((result) => {
      if (result.value) {
        console.log(result.value);
        this.mainService.rejectBooking(this.getCurrentBooking(), result.value).subscribe(data => {
          if(data.output) {
            Swal.fire({
              title: 'Success!',
              text: 'A booking has been rejected succesfully!!',
              icon: 'warning',
              confirmButtonText: 'Close'
            }).then(value => {
              this.getAllBookings();
            });
          } else {
            this.errorAlert()
          }
        })
      }
    })
  }

  closeBooking(data) {
    Swal.fire({
      title: 'Are you sure want close this booking?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      showLoaderOnConfirm: true
    }).then((result) => {
      if (result.value) {
        this.mainService.closeBooking(data).subscribe(data => {
          if(data.output) {
            Swal.fire({
              title: 'Success!',
              text: 'A booking has been closed succesfully!!',
              icon: 'success',
              confirmButtonText: 'Close'
            }).then(value => {
              this.getAllBookings();
            });
          } else {
            this.errorAlert()
          }
        })
      }
    })
  }

  switchTab(tab) {
    let tabs = document.getElementById('sidenav').children;
    for (let i = 0; i < tabs.length; i++) {
      if(tabs[i].classList.contains('active')) {
        $(tabs[i]).removeClass('active');
        break;
      }
    }
    tab.classList.add('active');
    let contents = document.getElementById('main-section').children as HTMLCollectionOf<HTMLElement>;
    for (let i = 0; i < contents.length; i++) {
      contents[i].style ? contents[i].style.display = 'none' : "";
    }
    let content = document.getElementById(tab.attributes['data-target'].value);
    content.style.display = "block";
  }

  onSubmit(form) {
    this.mainService.addVolunteer(this.formatData(form.value), form.value.phone).subscribe(data => {
      if(data.output) {
        Swal.fire({
          title: 'Success!',
          text: 'Volunteer added succesfully!',
          icon: 'success',
          confirmButtonText: 'Close'
        }).then(value => {
          this.getAllVolunteer()
        });
      }
    })
  }

  formatData(item: any) {
    return {
      phoneNumber: item.phone,
      alt_phoneNumber: item.altPhone,
      name: item.name,
      address1: item.line1,
      address2: item.line2,
      district: item.district,
      state: item.state,
      pincode: item.pincode
    }
  }

  saveStock() {
    this.mainService.saveStock(this.stockForm).subscribe(data => {
      if(data.output) {
        $('#stockModal').modal('hide');
        Swal.fire({
          title: 'Success!',
          text: 'Stock saved succesfully!',
          icon: 'success',
          confirmButtonText: 'Close'
        }).then(value => {
          this.stockForm = {};
          this.getAvailableStock();
        });
      }
    })
  }

  logout() {
    localStorage.clear();
    this.router.navigate(['login']);
  }

  errorAlert() {
    Swal.fire({
      title: 'Error!',
      text: 'Something went wrong!!',
      icon: 'error',
      confirmButtonText: 'Close'
    })
  }

  onGridReady(params) {
    this.gridApi = params.api;
    // this.http
    //   .get('https://www.ag-grid.com/example-assets/olympic-winners.json')
    //   .subscribe((data) => {
    //     this.rowData = data;
    //   });
  }

}
