import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { MainService } from '../../shared/services/main.service';
// import { ICellRendererParams, IAfterGuiAttachedParams } from 'ag-grid';

@Component({
  selector: 'app-btn-cell',
  templateUrl: './btn-cell.component.html',
  styleUrls: ['./btn-cell.component.scss']
})
export class BtnCellComponent implements ICellRendererAngularComp {

  params;
  label: string;

  agInit(params): void {
    this.params = params;
    this.label = this.params.label || null;

    if(params.data.status == 'delivered') {
      let exp_return_date = new Date( (new Date(params.data.exp_return_date)).toDateString() );
      let today = new Date( (new Date()).toDateString() );
      if(today >= exp_return_date) {
        params.close = true;
        params.data.status = 'to be returned';
      } else {
        params.data.status = 'delivered';
      }
    }
  }

  refresh(params?: any): boolean {
    return true;
  }

  onClick($event, btn) {
    console.log(this.params)
    if(btn == 'edit') {
      this.params.parent.editVolunteer(this.params.data);
    }
    if(btn == 'viewV') {
      this.params.parent.viewVolunteer(this.params.data);
    }
    if(btn == 'viewB') {
      this.params.parent.viewBooking(this.params.data);
      this.params.parent.setCurrentBooking(this.params.data);
    }
    if(btn == 'closeB') {
      this.params.parent.closeBooking(this.params.data);
    }
    if (this.params.onClick instanceof Function) {
      // put anything into params u want pass into parents component
      const params = {
        event: $event,
        rowData: this.params.node.data
        // ...something
      }
      this.params.onClick(params);
    }
  }

}
