import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from "../../../environments/environment";
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MainService {

  constructor(
    public http: HttpClient
  ) { }

  sendOtp(mobile: any): Observable<any> {
    let url = `${environment.baseUrl}/api/login/sendOTP/${mobile || ""}`;
    return this.http.get(url).pipe(data => {
      return data;
    })
  }

  verifyOtp(mobile: any, otp: any): Observable<any> {
    let url = `${environment.baseUrl}/api/login/verifyOTP/${mobile || ""}/${otp || ""}`;
    return this.http.get(url).pipe(data => {
      return data;
    })
  }

  updateInfo(item: any, mobile: any): Observable<any> {
    let url = `${environment.baseUrl}/api/login/updateInfo/${mobile || ""}`;
    return this.http.post(url, this.formatData(item)).pipe(data => {
      return data;
    })
  }

  addVolunteer(item: any, mobile: any): Observable<any> {
    let url = `${environment.baseUrl}/api/volunteer/updateInfo/${mobile || ''}`
    return this.http.post(url, item).pipe(data => {
      return data;
    })
  }

  getAllVolunteer(): Observable<any> {
    let url = `${environment.baseUrl}/api/volunteer`;
    return this.http.get(url).pipe(data => {
      return data;
    })
  }

  getAllBookings(): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket`;
    return this.http.get(url).pipe(data => {
      return data;
    })
  }

  createBookings(mobile: any, need: any): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket/create/${mobile || ''}`;
    let item = {
      phoneNumber: mobile,
      Number_of_cylinder: (need == "Oxygen Concentrators") ? 1 : 0,
      Number_of_monitorKid: (need == "Oxygen Concentrators") ? 0 : 1
    }
    return this.http.post(url, item).pipe(data => {
      return data;
    })
  }

  getAvailableStock(): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket/available`;

    return this.http.get(url).pipe(data => {
      return data;
    })
  }

  approve(item: any): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket/approve`;
    let out = {
      phoneNumber: item.phoneNumber,
      Number_of_cylinder: item.Number_of_cylinder,
      Number_of_monitorKid: item.Number_of_monitorKid,
      _id: item._id
    }
    return this.http.post(url, out).pipe(data => {
      return data;
    })
  }

  rejectBooking(item: any, reason: any): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket/reject`;
    let out = {
      phoneNumber: item.phoneNumber,
      Number_of_cylinder: item.Number_of_cylinder,
      Number_of_monitorKid: item.Number_of_monitorKid,
      _id: item._id,
      reason: reason
    }
    return this.http.post(url, out).pipe(data => {
      return data;
    })
  }

  closeBooking(item: any): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket/close`;
    let out = {
      phoneNumber: item.phoneNumber,
      Number_of_cylinder: item.Number_of_cylinder,
      Number_of_monitorKid: item.Number_of_monitorKid,
      _id: item._id
    }
    return this.http.post(url, out).pipe(data => {
      return data;
    })
  }

  saveStock(item: any): Observable<any> {
    console.log('service', item)
    let url = `${environment.baseUrl}/api/ticket/saveStock`;
    let out = {
      cylinder: item.oc || 0,
      meter: item.po || 0
    }
    return this.http.post(url, out).pipe(data => {
      return data;
    })
  }

  dashboard(): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket/getDashboard`;

    return this.http.get(url).pipe(data => {
      return data;
    })
  }

  getReports(): Observable<any> {
    let url = `${environment.baseUrl}/api/ticket/getReports`;

    return this.http.get(url).pipe(data => {
      return data;
    })
  }

  formatData(item) {
    return {
      Patient_Name: item.name,
      Gender: item.gender,
      Age: item.age,
      SPO2: item.spo2,
      Comorbidities: item.comorbidities,
      Symptoms: item.symptoms,
      CovidSince: item.covidSince,
      Address: item.address,
      Landmark: item.landmark,
      img: item.image
    };
  }

}
